body{
	background-color: #24272E
}

.header {
	background: white;
	padding: 20px;
}

.header h1 {
  color: #24272E;
	text-align: center;
	font-family: Asap, sans-serif;
}

.score-board {
	margin: 20px auto;
	border: 3px solid white;
	border-radius: 4px;
	text-align: center;
	width: 200px;
	color: white;
	font-size: 46px;
	padding: 15px 20px;
	font-family: Asap, sans-serif;
	position: relative;
}

.badge {
	background: #E25840;
	color: white;
	font-size: 14px;
	padding: 2px 10px;
	font-family: Asap, sans-serif;
}

.user-label {
	position: absolute;
	top: 30px;
	left: -20px;
}

.computer-label {
	position: absolute;
	top: 30px;
	right: -20px;
}

.result {
	font-size: 40px;
	color: white;
}

.result p {
	text-align: center;
	font-weight: bold;
	/* font-family: Asap, sans-serif; */
}

.choices {
	margin-top: 50px;
	text-align: center;
}

.choice {
	border: 4px solid white;
	border-radius: 50%;
	margin: 0 20px;
	padding: 15px;
	display: inline-block;
	transition: all 0.2s ease;
}

.choice:hover {
	cursor: pointer;
	background: red;
}

.action-message {
	text-align: center;
	font-weight: bold;
	color: white;
	font-family: Asap, sans-serif;
	font-size: 20px;
	margin-top: 20px;
}

.main img {
  width: 60px;
  height: 60px;
}